<template>
  <div class="page-body">
    <van-nav-bar
      fixed
      title="售后服务"
      left-arrow
      @click-left="$router.back()"
    ></van-nav-bar>
    <!-- 商品详情 -->
    <div class="goods-detail dis-flex">
      <div class="left">
        <img class="goods-image" :src="goods.goods_image" />
      </div>
      <div class="right">
        <div class="goods-name">
          <span>{{ goods.goods_name }}</span>
        </div>
        <div class="dis-flex goods-num">
          <span class="t-r">×{{ goods.total_num }}</span>
        </div>
      </div>
    </div>

    <!-- 服务类型 -->
    <div class="row-service">
      <div class="row-title">服务类型</div>
      <div class="service-switch dis-flex">
        <div
          class="switch-item"
          v-for="(item, index) in RefundType"
          :key="index"
          :class="{ active: formData.type == item.value }"
          @click="onSwitchService(item.value)"
        >
          {{ item.name }}
        </div>
      </div>
    </div>

    <!-- 申请原因 -->
    <div class="row-textarea">
      <div class="row-title">申请原因</div>
      <div class="content">
        <textarea
          class="textarea"
          v-model.trim="formData.content"
          maxlength="2000"
          placeholder="请详细填写申请原因，注意保持商品的完好，建议您先与卖家沟通"
          placeholderStyle="color:#ccc"
        ></textarea>
      </div>
    </div>

    <!-- 退款金额 -->
    <div v-if="formData.type == 10" class="row-money dis-flex">
      <div class="row-title">退款金额</div>
      <div class="money">￥{{ goods.total_pay_price }}</div>
    </div>

    <!-- 上传凭证 -->
    <div class="row-voucher">
      <div class="row-title">上传凭证 (最多6张)</div>
      <van-uploader v-model="imageList" :after-read="uploadImage" multiple />
    </div>

    <!-- 底部操作按钮 -->
    <div class="footer-fixed">
      <div class="btn-wrapper">
        <div class="btn-item btn-item-main" @click="onSubmitRefund()">
          确认提交
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getRefundGoodsDetailApi, onSubmitRefundApi } from '@/api/refund'
import { uploadImageApi } from '@/api/orderList'
import { Toast } from 'vant'

export default {
  name: 'ApplyRefund',

  data () {
    return {
      orderGoodsId: this.$route.query.orderGoodsId,
      goods: {},

      formData: {
        images: [],
        type: 10,
        content: ''
      },

      RefundType: [
        { key: 'RETURN', name: '退货退款', value: 10 },
        { key: 'EXCHANGE', name: '换货', value: 20 }
      ],

      imageList: []
    }
  },

  methods: {
    async getRefundGoodsDetail () {
      const { data: { goods } } = await getRefundGoodsDetailApi(this.orderGoodsId)
      this.goods = goods
    },

    onSwitchService (value) {
      this.formData.type = value
    },

    async uploadImage (file) {
      const formData = new FormData()
      formData.append('file', file.file)
      const { data: { fileInfo } } = await uploadImageApi(formData)
      // console.log(fileInfo)
      this.formData.images.push(fileInfo.file_id)
    },

    async onSubmitRefund () {
      if (!this.formData.content) {
        Toast('请输入申请原因')
        return
      }
      await onSubmitRefundApi(this.orderGoodsId, this.formData)
      Toast('已提交申请')
      this.$router.replace('/refund')
    }
  },

  created () {
    this.getRefundGoodsDetail()
  }
}

</script>

<style lang="less" scoped>
.page-body {
  padding: 48px 21px;
}
.dis-flex {
  display: flex;
}
.t-r {
  text-align: right;
}

.row-title {
  color: #888;
  margin-bottom: 10px;
}

// 商品信息
.goods-detail {
  padding: 12px 10px;

  .left {
    .goods-image {
      width: 75px;
      height: 75px;
    }
  }

  .right {
    padding-left: 10px;
    .goods-name {
      padding-bottom: 15px;
    }
    .goods-num {
      display: flex;
      justify-content: end;
      color: #888;
    }
  }
}

/* 服务类型 */
.row-service {
  padding: 12px 10px;
}

.service-switch {
  .switch-item {
    padding: 3px 15px;
    margin-right: 12px;
    border-radius: 5px;
    border: 1px solid rgb(177, 177, 177);
    color: #888;

    &.active {
      color: #fc1e56;
      border: 1px solid #fc1e56;
    }
  }
}

/* 申请原因 */
.row-textarea {
  padding: 12px 10px;

  .textarea {
    width: 100%;
    height: 110px;
    padding: 6px;
    border: 1px solid #e8e8e8;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 13px;
  }
}

/* 退款金额 */
.row-voucher {
  padding: 12px 10px;
}
.row-money {
  padding: 12px 10px;

  .row-title {
    margin-bottom: 0;
    margin-right: 15px;
  }
  .money {
    color: #fa2209;
  }
}

// 底部操作栏
.footer-fixed {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 11;
  box-shadow: 0 -4px 40px 0 rgba(151, 151, 151, 0.24);
  background: #fff;

  .btn-wrapper {
    height: 60px;
    display: flex;
    align-items: center;
    padding: 0 10px;
  }

  .btn-item {
    flex: 1;
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    border-radius: 25px;
  }

  .btn-item-main {
    background: linear-gradient(to right, #f9211c, #ff6335);
  }
}
</style>
